import React, { Component } from 'react';
import $ from 'jquery';

class Careers extends Component {
	constructor(){
			super();
			this.state={
				title:[],
				work:[],
				con2:[]
			}
		  };
    componentDidMount = function () {
    	$.ajax({
				'url':'http://192.168.43.25:8100/cebest/careers1',
				'type':'get',
				'success':function(opt){
					// console.log(opt)
					this.setState({
						title:opt
					})
					$(".my-work h1").html(this.state.title[0].title)
					$(".my-work p").html(this.state.title[0].con)
					$(".my-why-mask h2").html(this.state.title[1].title)
					$(".my-why-mask p").html(this.state.title[1].con)
					$(".my-job-l h2").html(this.state.title[2].title)
					$(".my-job-l p").html(this.state.title[2].con)
				}.bind(this)
			})
    	
    	
    	$.ajax({
				'url':'http://192.168.43.25:8100/cebest/careers2',
				'type':'get',
				'success':function(opt){
                    // console.log(opt)
					this.setState({
						con2:opt
					})

				}.bind(this)
			})
    	

        $.ajax({
            'url':'http://192.168.43.25:8100/cebest/careers',
            // 'url':'http://127.0.0.1:8100/cebest/careers',
            'type':'get',
            'success':function(data){
                var arr=[]
                // console.log(data)
                this.setState({
                    work:data
                })
                $(".my-job-r ul li").click(function () {
                    $(this).find(".my-detail").stop(true).fadeToggle().parent().find("img").toggleClass("rot")
                })
                $(".my-job-r ul li").children(".my-detail").click(function (ev) {
                    var e=ev||window.event
                    if(e.stopPropagation){
                        //ie
                        e.stopPropagation()
                    }else{
                        e.cancelBubble=true
                    }
                })
            }.bind(this)
        }) 



    	/*$.ajax({
				'url':'http://192.168.43.25:8100/cebset/careers',
				'type':'get',
				'success':function(opt){
					for(var i=0;i<opt.length;i++){
						if(opt[i].work!=null){
							this.state.work.push(opt[i].work)
						}
					}
					console.log(opt)
				}.bind(this)
			})*/
        setTimeout(function () {
            $(".my-talk_window").show()
        },200)
    };

    render() {
        return (
            <div className="my-careers">
                {/*work start*/}
                <div className="my-work">
                    <h1></h1>
                    <img src="images/ly_img07.png" alt=""/>
                    <p></p>
                    <a href="">
                        <span className="my-work-span-one"></span>
                        <span className="my-work-span-two"></span>
                    </a>
                </div>
                {/*work end*/}
                {/*hr start*/}
                <div className="my-hr">
                    <ul className="clear">
                    {this.state.con2.map(function(v,i){
                    	return <li key={i}>
                            <img src={v.img} alt=""/>
                            <h2>{v.title1}</h2>
                            <h3>{v.title2}</h3>
                            <img src="images/ly_img08.png" alt=""/>
                            <p>{v.con}</p>
                        </li>
                    })}
                    </ul>
                </div>
                {/*hr end*/}
                {/*why start*/}
                <div className="my-why">
                    <div className="my-why-mask">
                        <h2></h2>
                        <img src="images/ly_img07.png" alt=""/>
                        <p></p>
                        <a href="">
                            <span className="my-why-span-one"></span>
                            <span className="my-why-span-two"></span>
                        </a>
                    </div>
                </div>
                {/*why end*/}
                {/*job start*/}
                <div className="my-job clear">
                    <div className="my-job-l left">
                        <div>
                            <h2></h2>
                            <img src="images/ly_img08.png" alt=""/>
                            <p></p>
                        </div>
                    </div>
                    <div className="my-job-r left">
                        <div>
                            <ul>
                            {
                                    this.state.work.map(function (v,i) {
                                        return <li key={i}>
                                            <h2><span>{v.work}</span><span>{v.duty}</span><img src="images/ly_icon38.png" alt=""/></h2>
                                            <div className="my-detail">
                                                <h3></h3>
                                                 {v.titlea.split("?").map(function (v,i) {
                                                     return <div key={i}>
                                                         <p>{i+1}<span>{v}</span></p>
                                                     </div>
                                                 })}
                                                <h3></h3>
                                                {v.titleb.split("?").map(function (v,i) {
                                                    return <div key={i}>
                                                        <p>{i+1}<span>{v}</span></p>
                                                    </div>
                                                })}
                                                <a href="">
                                                    <span className=""></span>
                                                    <span></span>
                                                </a>
                                            </div>
                                        </li>
                                    })
                                }
                                
                                {/*
                                 <li>
                                 <h2><span></span>Account Manager <img src="images/ly_icon38.png" alt=""/></h2>
                                 <div className="my-detail">
                                 <h3></h3>
                                 <div>
                                 <p>1<span></span></p>
                                 </div>
                                 <div>
                                 <p>2<span></span></p>
                                 </div>
                                 <div>
                                 <p>3<span></span></p>
                                 </div>
                                 <div>
                                 <p>4<span></span></p>
                                 </div>
                                 <div>
                                 <p>5<span></span></p>
                                 </div>
                                 <h3></h3>
                                 <div>
                                 <p>1<span></span></p>
                                 </div>
                                 <div>
                                 <p>2<span></span></p>
                                 </div>
                                 <div>
                                 <p>3<span></span></p>
                                 </div>
                                 <div>
                                 <p>4<span></span></p>
                                 </div>
                                 <div>
                                 <p>5<span></span></p>
                                 </div>
                                 <div>
                                 <p>6<span></span></p>
                                 </div>
                                 <a href="">
                                 <span className=""></span>
                                 <span></span>
                                 </a>
                                 </div>
                                 </li>
                                */}
                            </ul>
                        </div>
                    </div>
                </div>
                {/*job end*/}
            </div>
        )
    }
}
export default Careers


